.product-card {
  background-color: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.product-image {
  width: 100%;
  max-height: 20vh;
  object-fit: cover;
}

.product-info {
  padding: 1rem;
  font-size: 12px;
  display: flex;
  flex-direction: column;
}

.product-info h1{
  margin: 0%;
}

.product-brand {
  font-size: 0.875rem;
  font-weight: bold;
  margin: 0 0 0.25rem;
}

.product-name {
  font-size: 0.875rem;
  color: #52525b;
  margin: 0 0 0.5rem;
  min-height: 40px;
}

.product-price {
  font-size: 1.125rem;
  font-weight: bold;
  margin: 0 0 1rem;
}

.product-actions {
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
}

.add-to-cart-btn, .purchase-btn {
  flex: 1;
  padding: 0.75rem 0;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
}

.add-to-cart-btn {
  background-color: #333;
  color: white;
}

.purchase-btn {
  background-color: #fde047;
  color: #111;
  text-decoration: none;
}

.add-to-cart-btn:hover {
  background-color: #3f3f46;
}
